 *{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.dogs,
.create,
.about {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.dogs {
  background-color: #E9C46A;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color:black;
  font-size: 100px;
}

.create {
  background-color: #E9C46A;
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: black;
  font-size: 100px;
}

.about {
  background-color: #E9C46A;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: black;
  font-size: 100px;
}